@charset "utf-8";

* {
  box-sizing: border-box;
  font-family: $theme-font;
}

html, body {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h5, p {
  margin: 0;
}

h1, h2, h3, h4, h5, h5, p, a {
  font-weight: 400;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}